<template>
  <div>
    <div class="flex-row">
      <p class="slider-label">speed</p>
      <vue-slider class="slider flex-one" v-model="tempo" :height="4" :min="50" :max="300" :processStyle="sliderColor" :tooltipStyle="sliderColor"></vue-slider>
    </div>
    <select v-model="scale">
      <option v-for="scaleOption in Object.keys(midi.scales)" :value="scaleOption" :selected="scaleOption === scale">{{midi.scales[scaleOption].name}}</option>
    </select>
  </div>
</template>

<script>
import VueSlider from 'vue-slider-component'

export default {
  name: 'music-controls',
  data () {
    return {
      tempo: 160,
      scale: 'major_pentatonic'
    }
  },
  props: ['midi'],
  computed: {
    sliderColor () {
      return {'background-color': '#ff2200', 'border-color': '#f20'}
    }
  },
  watch: {
    tempo (value) {
      this.midi.tempo = parseInt(value)
    },
    scale (value) {
      this.midi.scale = value
    }
  },
  components: { VueSlider }
}
</script>

<style lang="less" scoped>
@import '../../static/less/flex';
@import '../../static/less/common';

.slider {
  margin-top: 30px;
  margin-bottom: 25px;
}
.slider-label {
  padding-top: 10px;
}
input {
  font-size: 16px;
  border-radius: 4px;
  border: solid 1px @color-blue;
  background-color: transparent;
  color: white;
  padding: 8px;
  &[name="tempo"] {
    width: 60px;
  }
}
select {
  border: solid 1px @color-blue;
  padding: 5px;
  font-size: 18px;
  height: 36px;
  background-color: transparent;
  color: white;
  &:focus {
    outline: none;
  }
}
</style>



